<template>
  <div class="top">
      <div class="login">
        <div class="title">Login</div>
        <input type="text" v-model="username" placeholder="Username">
        <input type="passworld" v-model="password" placeholder="Password">
        <button @click="add">Login</button>
      </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      username:'',
      password:'',
    }

    },
    methods:{
      async add(){
        const login ={username:this.username,password:this.password}
      const res = await this.$http.post('/login',login)
      localStorage.token=res.data.token //数据存到localStorage 
      this.$router.push('/')
      }
  }
}
</script>
